.steps-ctrl {
  width: 100%;
  font-size: 12px;
  margin-left: 25px;
}
.steps-ctrl .title {
  color: #333;
}
.steps-ctrl .steps-wrap {
  height: 57px;
  display: flex;
  align-items: center;
}
.steps-ctrl .steps-wrap .steps-item {
  display: flex;
  flex-direction: column;
  color: #c0c4cc;
  margin-left: 381px;
}
.steps-ctrl .steps-wrap .steps-item:first-child {
  margin-left: 0;
}
.steps-ctrl .steps-wrap .steps-item:first-child .round:before {
  display: none;
}
.steps-ctrl .steps-wrap .steps-item.active .stepsTxt,
.steps-ctrl .steps-wrap .steps-item.active .round {
  color: #1989fa;
  font-weight: 500;
}
.steps-ctrl .steps-wrap .steps-item.active .round {
  border-color: #1989fa;
}
.steps-ctrl .steps-wrap .steps-item.active .round:before {
  background-color: #1989fa;
}
.steps-ctrl .steps-wrap .steps-item.wait .stepsTxt,
.steps-ctrl .steps-wrap .steps-item.wait .round {
  color: #303133;
  font-weight: 500;
}
.steps-ctrl .steps-wrap .steps-item.wait .round {
  border-color: #303133;
}
.steps-ctrl .steps-wrap .steps-item .round {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 2px solid;
  border-color: #c0c4cc;
  position: relative;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
}
.steps-ctrl .steps-wrap .steps-item .round:before {
  display: block;
  content: "";
  width: 388px;
  height: 2px;
  background-color: #c0c4cc;
  position: absolute;
  right: 22px;
  top: 9px;
}
.steps-ctrl .steps-wrap .steps-item .stepsTxt {
  color: #c0c4cc;
}
.steps-ctrl .toNextBtn {
  margin-top: 12px;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dcdfe6;
  border-color: #dcdfe6;
}
