


























































 .steps-ctrl {
  width: 100%;
  font-size: 12px;
  margin-left: 25px;
  .title {
    color: #333;
  }
  .steps-wrap {
    height: 57px;
    display: flex;
    align-items: center;
    .steps-item {
      display: flex;
      flex-direction: column;
      color: #c0c4cc;
      margin-left: 381px;
      &:first-child {
        margin-left: 0;
        .round:before {
          display: none;
        }
      }
      &.active {
        .stepsTxt,
        .round {
          color: #1989fa;
          font-weight: 500;
        }
        .round {
          border-color: #1989fa;
          &:before {
            background-color: #1989fa;
          }
        }
      }
      &.wait {
        .stepsTxt,
        .round {
          color: #303133;
          font-weight: 500;
        }
        .round {
          border-color: #303133;
        }
      }
      .round {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        border: 2px solid;
        border-color: #c0c4cc;
        position: relative;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        &:before {
          display: block;
          content: "";
          width: 388px;
          height: 2px;
          background-color: #c0c4cc;
          position: absolute;
          right: 22px;
          top: 9px;
        }
      }
      .stepsTxt {
        color: #c0c4cc;
      }
    }
  }
  .toNextBtn {
    margin-top: 12px;
    padding: 12px 20px;
    font-size: 14px;
    border-radius: 4px;
    text-align: center;
    box-sizing: border-box;
    cursor: pointer;
    background: #fff;
    border: 1px solid #dcdfe6;
    border-color: #dcdfe6;
  }
} 

